/*-----------------burger-----------*/

.burger {
width: 30px;
height: 23px;
position: relative;
cursor: pointer;
transition-duration: 0.3s;
display: none;
z-index: 33;
@media (max-width:867px) {
  display: block;
}
}

.burger::before,
.burger::after {
position: absolute;
content: '';
width: 30px;
height: 3px;
border-radius: 5px;
background-color: #FFC522;
transition-duration: 0.3s;
}

.burger::before {
top: 0;
}

.burger::after {
bottom: 0;
}

.burger span {
position: absolute;
width: 30px;
height: 3px;
border-radius: 5px;
background-color: #FFC522;
top: 50%;
margin-top: -1px;
transition-duration: 0.3s;
}

.burger.active span {
transform: scale(0);
}

.burger.active::before {
top: 50%;
margin-top: -1px;
transform: rotate(-45deg);
}

.burger.active::after {
top: 50%;
margin-top: -1px;
transform: rotate(45deg);
}
